.usermangemt {
  height: calc(100vh - 225px);
  overflow: auto;
  padding: 12px;
}

.outer.outpart {
  height: calc(100vh - 200px);
}

table {
  width: 100%;
}

.select-outer {
  float: right;
}

.heading {
  margin: 0;
  font-size: 17px;
  font-family: "GothamNarrowBold", sans-serif;
  font-weight: bold;
}

.color1 {
  background: red;
  height: 34px !important;
  display: inline-block;
  align-items: flex-end;
  vertical-align: middle;
}

th.mat-sort-header-sorted {
  color: black;
}

.links {
  color: #0075bc;
  cursor: pointer !important;
  text-decoration: underline;
}

.adduser-btn {
  background-color: #ff6400 !important;
  color: #fff !important;
  height: 34px !important;
  padding: 0px 30px !important;
  border-radius: 6px !important;
  line-height: 34px;
}

.view-user .mat-form-field-infix {
  display: block;
  position: relative;
  flex: auto;
  min-width: 0;
  width: 115px;
}

.view-user .mat-form-field-appearance-outline .mat-form-field-infix {
  padding: 4px 6px 7px 4px;
  margin: 0 !important;
  top: -2px;
  font-size: 14px;
}

.view-user .mat-select-value {
  color: #585858;
}

.view-user .mat-card {
  padding: 0;
  border-radius: 10px 10px 0 0;
}

.edit-profile .mat-form-field-wrapper {
  padding-bottom: 0px;
}

.cancel-btn {
  background-color: #9e9e9e;
  min-width: 125px;
}

.save-change {
  background-color: #ff6400;
  color: #fff;
  min-width: 125px;
}

.editicon .mat-icon-button.mat-accent {
  color: #1072ce !important;
}

.deleteicon .mat-icon-button.mat-accent {
  color: #1072ce !important;
  margin-right: 10px;
}

.usermangemt table tr th:last-child,
.usermangemt table tr td:last-child {
  width: 100px;
}

.usermangemt table tr th:nth-of-type(5),
.usermangemt table tr td:nth-of-type(5) {
  width: 190px;
  text-align: left;
}

.usermangemt table tr th:nth-of-type(2),
.usermangemt table tr td:nth-of-type(2) {
  width: 126px;
}

.usermangemt table tr td:last-child {
  text-align: center;
}

.usermangemt table tr th:last-child {
  padding-right: 30px !important;
}

.delbtn {
  background: #1072ce !important;
}

.norecord {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 10px;
  border: 1px solid #ccc9;
  border-top: transparent;
  background-color: #fbfbfb;
}

.custom-error {
  color: red;
  display: block;
  font-size: 11px;
  position: absolute;
  left: 26px;
}

table {
  border-collapse: separate;
  border-spacing: 0 1em;
}

.user-section table.mat-table {
  border-collapse: separate !important;
  border-spacing: 0 12px !important;
}

.usermangemt table tr td:first-child {
  border-radius: 10px 0 0 10px !important;
}

.userpart .usermangemt table tr td {
  background: #fff !important;
  border-radius: 10px !important;
}

.usermangemt table tbody tr {
  box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.19) !important;
}

.userpart .usermangemt tr.mat-footer-row,
.userpart .usermangemt tr.mat-row {
  height: 60px;
}

/* toggle */
.usermangemt tr th:first-child {
  border-radius: 5px 0 0 0px !important;
}

.usermangemt tr th:last-child {
  border-radius: 0px 5px 0 0px !important;
}

.name_outer {
  line-height: 16px;
}

[hidden] {
  display: none !important;
}

.msg {
  font-family: "GothamNarrowBold", sans-serif;
}

.userpart .usermangemt table tr td {
  width: 16.66% !important;
  padding-right: 15px;
}

.usermangemt table tr th:last-child {
  text-align: center;
  padding-left: 15px;
}

/* .mat-form-field {
  width: 85% !important;
} */

.edit-profile label {
  font-size: 13px;
}

.userlist {
  padding: 20px 58px 20px 20px !important;
}

/*responsive css start*/
@media screen and (max-width: 767px) {

  table,
  thead,
  tbody,
  th,
  td,
  tr {
    display: inline-block;
  }

  .del_doc {
    width: 100% !important;
  }

  .norecord tr td {
    padding: 0;
    border-bottom: none;
    border-top: transparent;
    background-color: transparent;
  }

  .norecord tr {
    border: none;
  }

  thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }

  tr {
    border: 1px solid #ccc;
  }

  td {
    border: none;
    border-bottom: 1px solid #eee;
    position: relative;
    padding-left: 50%;
  }

  td span.textTtl {
    display: inline-block !important;
    position: absolute;
    top: 50%;
    left: 25px;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
    transform: translate(0, -50%);
    font-weight: bold;
    color: #000;
  }

  .usermangemt table tr {
    height: 100%;
    margin-bottom: 25px;
  }

  .usermangemt table tr {
    border: none;
    width: 100%;
  }

  .usermangemt table tr th {
    display: none;
  }

  .btn-toggle:after {
    width: 5rem;
  }

  .usermangemt table tr td {
    width: 100% !important;
    padding-left: 45% !important;
    text-align: left !important;
    height: 45px;
    word-break: break-all;
    line-height: 12px;
    padding-top: 15px;
    padding-right: 8px;
    border: none;
    font-size: 13px;
  }

  .usermangemt table tr td:last-child {
    padding-top: 5px;
  }

  .usermangemt table tr td:nth-of-type(2) {
    display: flex;
    flex-direction: column;
    line-height: 1.1 !important;
    padding-top: 15px;
    justify-content: center;
    padding-bottom: 20px;
  }

  .modal-dialog {
    max-width: auto !important;
    min-width: auto !important;
    margin: 0 auto;
  }

  .edit-profile .col {
    min-width: 100%;
    margin-bottom: 28px;
  }

  .edit-profile .col:nth-child(3) {
    margin-bottom: 0;
  }

  .edit-profile .cancel-btn {
    min-width: 0;
    padding: 10px 32px;
    line-height: 14px !important;
    left: 3px;
  }

  .edit-profile .save-change {
    min-width: 0;
    padding: 10px 5px;
    line-height: 14px !important;
    margin-bottom: 10px;
  }

  .edit-profile .col .mat-form-field {
    width: 100% !important;
  }

  .edit-mob-view .col {
    min-width: 32% !important;
  }

  /*new*/
  tr.mat-header-row {
    height: 0 !important;
    display: none;
  }

  .usermangemt table {
    background: transparent;
  }

  .view-user .mat-card {
    background: transparent;
    box-shadow: none !important;
  }

  .usermangemt table tr td:first-child {
    border-radius: 10px !important;
  }

  .edit-profile label {
    font-size: 12px;
    margin-right: 0.3rem !important;
  }

  .linkpart button {
    padding: 10px 22px !important;
  }

  .userlist {
    padding: 20px 0 0 0 !important;
  }

  .outer.outpart {
    padding: 0 20px;
  }

  .title-container {
    margin: 0 0 20px;
  }

  .page-title {
    width: 100%;
    display: block;
    border-radius: 0;
    border-top: 2px solid #ccc;
    border-bottom: 2px solid #ccc;
    font-size: 17px;
  }

  .userlist .ques-icon .qicon {
    bottom: 18px;
  }
}

@media screen and (min-width: 320px) and (max-width: 374px) {
  .usermangemt table tr td {
    padding-left: 35% !important;
    padding-right: 5px;
    border: none;
    font-size: 12px;
  }

  .usermangemt table tr td:nth-of-type(2) {
    display: flex;
    flex-direction: column;
    line-height: 1.1 !important;
    padding-top: 15px;
    justify-content: center;
    padding-bottom: 20px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .usermangemt table tr td {
    padding-right: 10px;
  }
}

@media screen and (max-width: 400px) {
  .usermangemt table tr td {
    padding-left: 36% !important;
  }
}

.ques-icon {
  position: relative;
}

.ques-icon .qicon {
  position: absolute;
  bottom: 0;
  right: 12px;
  cursor: pointer;
}

.mat-slide-toggle-bar,
.mat-slide-toggle.mat-checked .mat-slide-toggle-bar {
  border-radius: 1.5rem;
  background: #e8e8e8;
  box-shadow: none;
  margin: 0 1.3rem !important;
}

.mat-slide-toggle.mat-checked .mat-slide-toggle-thumb,
.mat-slide-toggle-thumb {
  border-radius: 1.125rem;
  background: #3da3e1;
}

#status-toggle {
  display: flex;
  align-items: center;
  vertical-align: middle;
  height: inherit;
  width: 250px !important;
}

.mat-slide-toggle-content {
  overflow: inherit !important;
}
.save-change.delbtn {
  background: #1c6fb1 !important;
}
.cancel-btn {
  background: #a7a4a4 !important;
}
.save-change {
  background: #ff6400 !important;
}
.edit-profile button {
  min-width: 120px !important;
}